<script>
import { BAlert, BPagination, BFormInput, BLink, BFormSelect, BButton, BRow, BCol, BCard, BBadge, BTableSimple, BThead, BTbody, BTr, BTh, BTd } from 'bootstrap-vue';
import { mapActions, mapGetters, mapState } from 'vuex';

export default {
  computed: {
    ...mapGetters('stakingStore', ['stakeData']),
    filterStates() {
      return this.states.filter((item) => {
        return item.title.toLowerCase().includes(this.state.toLowerCase());
      });
    },
  },
  data() {
    return {
      selected: 'Free',
      email: '',
      modal: false,
      states: 'searchData',
      options: [
        { value: 'Free', text: 'Free' },
        { value: 'Real', text: 'Real' },
      ],
      errorfind: false,
    };
  },
  watch: {
    selected(newValue, oldValue) {
      // This method will be triggered when the selected option changes
      console.log('Selected option changed from', oldValue, 'to', newValue);
      if (newValue == 'Real') {
        this.getListReal();
      }
      if (newValue == 'Free') {
        this.getListFree();
      }
    },
  },
  methods: {
    ...mapActions('stakingStore', ['getNextData', 'getNextData', 'getListFree', 'getListReal', 'changePage']),
    async prevPage() {
      await this.getNextData();
    },
    async nextPage() {
      await this.getNextData();
    },
  },

  components: {
    BRow,
    BCol,
    BCard,
    BBadge,
    BTableSimple,
    BThead,
    BTbody,
    BTr,
    BTh,
    BTd,
    BButton,
    BFormSelect,
    BFormInput,
    BLink,
    BPagination,
    BAlert,
  },
};
</script>
